.app-wrapper {
    width: 100%;
    margin: 0 auto;
    /*background-color: black;
    color: white;*/
}

.app-wrapper-content {
    grid-area: m;
    display: grid;
}

.grid-container {
    display: grid;
    grid-template-columns: 0.06fr 1fr;
    grid-template-rows: 0.11fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
    "ml h h h"
    "m p p p"
}

.main_link {
    grid-area: ml;
    border: 2px solid green
}

.header {
    grid-area: h;
}

.menu {
    grid-area: m;
}

.main {
    display: grid;
    gap: 0px 0px;
    grid-area: p;
}





